import React, { Component } from 'react';
import TextBlock from '../TextBlock';
import CodeBlock from '../Codeblock';
import H1 from '../H1'

class Home extends Component {
  render () {
    return (
      <TextBlock>
          <div className="center-text">
            <H1>RAWRS</H1>
          </div>
          <br />
          <h2>React App With Routing and Styled-components</h2>
          <br />
          <p>Rawrs is a command line utility for React. It was made in an attempt to add to and improve the React ecosystem. It has much more functionality than Create React App, but much less than other, application specific boilerplates. This makes Rawrs the perfect choice for developers that are new to React.</p>
          <br />
          <pre className="w-half">
            <CodeBlock>
              npm i -g rawrs
            </CodeBlock>
          </pre>
          <br />
          <p className="center-text bold-text">Dependencies: ParcelJS</p>
          <br />
          <pre className="w-half">
            <CodeBlock>
              npm i -g parcel-bundler
            </CodeBlock>
          </pre>
          <br />
          <p>Rawrs has 5 main features:</p>
          <br />
          <ul>
            <li><a href="https://github.com/ReactTraining/react-router">React-Routing</a>- Such a necessary feature in many applications, Rawrs has routing implemented with example routes in the example scaffolding.</li>
            <li><a href="https://www.styled-components.com/docs">Styled-Components</a>- While other options such as SCSS are great, Styled Components enforces best practices- Small components, single use class names, splitting components and containers.</li>
            <li>Code Generation- Rawrs allows you to create components in the command line. This is something that other frameworks/libraries have, but is rare in the React ecosystem for some reason.</li>
            <li><a href="https://parceljs.org/">ParcelJS</a>- Rawrs is set up using ParcelJS, a zero configuration web application bundler that is much more user-friendly than webpack.</li>
            <li><a href="https://redux.js.org/">Redux</a>- Getting started with Redux is often difficult for beginners, so Rawrs has an option to build your app with Redux, helping by setting up the basic structure.</li>
          </ul>
          <br />
          <H1>Using Rawrs</H1>
          <br />
          <p>Rawrs command line tool gives you the ability to quickly add components to your project. Create functional or class components, with the option of adding redux.</p>
          <br />
          <pre>
            <CodeBlock>
          cd components<br />
          rawrs g TodoList<br />
          rawrs g TodoItem -f<br />
          rawrs g ItemCheckbox -f<br />
            </CodeBlock>
          </pre>
        <br />
        <p>For a more in-depth usage guide, see the <a href="/usage">Usage</a> page.</p>
      </TextBlock>
    );
  }
}

export default Home;
