import React, { Component } from 'react';
import TextBlock from '../TextBlock';
import H1 from '../H1';

class About extends Component {
  render () {
    return (
      <TextBlock>
        <H1>About This App</H1>
        <br />
        <p>Rawrs includes examples of state management, styled-components implementation, functional components, routing, lifecycle functions, actions passed as props, and more.</p>
        <br />
        <p>It's set up following modern React best practices. Small components, single use class names, and splitting containers and components. Rawrs is set up  so you can build React apps effectively and efficiently with DRY and reusable code.</p>
        <br />
        <H1>What Is Rawrs?</H1>
        <br />
        <p>Rawrs is an attempt to improve the React ecosystem, especially for new developers.</p>
        <br />
        <p>Create-react-app is great, but it doesnt even have routing. Its as barebones as you can get while still being a functional react app, and that may not be entirely helpful to beginners who have a working knowledge of React.</p>
        <br />
        <p>On the other end of the spectrum, you get boilerplates that throw in the kitcen sink; so many are so heavy. Those boilerplates are great if you really need to build a app that has very specific functionality, but their package.json files are as long as CVS receipts.</p>
        <br />
        <p>Rawrs has just enough to get you going, and then you can add whatever you need later as it comes up. And that is a philosophy as true in life as it is in programming; dont add stuff before its absolutely necessary.</p>
      </TextBlock>
    );
  }
}

export default About;